import React from 'react';
import "./display_profile.css"
import EndzoneNav from "../../components/navigators/endzone-nav/EndzoneNav"
import EndzoneTextInput from "../../components/form-inputs/text-input/endzone-text-input"
import EndzoneComboBox from '../../components/form-inputs/endzone-comboBox/endzone-combo-box';
import EndzoneButton from '../../components/buttons/endzone-new-button/endzone-button'
import PhoneInput from "react-phone-number-input";
import 'react-phone-number-input/style.css'
import { useState, useEffect } from 'react';

import { Alert } from '@mui/material';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import AddIcon from '@mui/icons-material/Add';

class DisplayProfile extends React.Component {

    constructor(props) {
        super(props);
        this.state = {firstName: "", lastName: "", email: "", phone: "", squads: [], curSquad: "", input: "", squadsList: [],
            alert: {
                style: {display: "none"},
                severity: "error",
                userMessage: ""
            }
        }
        this.httpHandler = require('react-http-client');
    }


    componentDidMount = () => {
        this.getProfileInformation();
        this.renderForm("profile");
    }

    sendUpdateRequestFiller = (e, body) => {
        return true;
    }

    sendUpdateRequest = (e, body) => {
        const request = this.httpHandler.post(
            "/account/user/profile/update", 
            {
                body
            }
        ).then(
            (request) => {
                console.log("success");
                return true;
            }, 
            (request) => {
                console.log("fail");
                return false;
            }
        );
    }


    getProfileInformationFiller = () => {
        this.setState({
            firstName: "Charlie",
            lastName: "Droege",
            email: "cdro@gmail.com",
            phone: "+12223334444",
            squads: ["varsity", "jv", "loser"],
            curSquad: "Varsity",
            squadsList: [{"text": "varsity", "value": "varsity"}, {"text": "jv", "value": "jv"}, {"text": "loser", "value": "loser"}]
        })
    }

    getProfileInformation(e) {
        const request = this.httpHandler.get(
            "/endzone/account/user/profile"
        ).then(
            (response) => {
                console.log(request)
                this.setState({
                    firstName:  response["first_name"],
                    lastName: response["last_name"],
                    email: response["email"],
                    phone: response["phone"],
                    squads: response["squads"],
                    curSquad: response["curSquad"],
                    squadsList: response['squadsList']
                })
            },
            (response) => {
                
            }
        );
    }

    renderForm(form) {
        if (form === "profile") {
            document.getElementById("profile-display").style.display = "inline-block";
            document.getElementById("name-form").style.display = "none";
            document.getElementById("email-form").style.display = "none";
            document.getElementById("phone-form").style.display = "none";
            document.getElementById("squad-form").style.display = "none";
        } else if (form === "name") {
            document.getElementById("profile-display").style.display = "none";
            document.getElementById("name-form").style.display = "inline-block";
            document.getElementById("email-form").style.display = "none";
            document.getElementById("phone-form").style.display = "none";
            document.getElementById("squad-form").style.display = "none";
        }
        else if (form === "email") {
            document.getElementById("profile-display").style.display = "none";
            document.getElementById("name-form").style.display = "none";
            document.getElementById("email-form").style.display = "inline-block";
            document.getElementById("phone-form").style.display = "none";
            document.getElementById("squad-form").style.display = "none";
        }
        else if (form === "phone") {
            document.getElementById("profile-display").style.display = "none";
            document.getElementById("name-form").style.display = "none";
            document.getElementById("email-form").style.display = "none";
            document.getElementById("phone-form").style.display = "inline-block";
            document.getElementById("squad-form").style.display = "none";
        }
        else if (form === "squad") {
            document.getElementById("profile-display").style.display = "none";
            document.getElementById("name-form").style.display = "none";
            document.getElementById("email-form").style.display = "none";
            document.getElementById("phone-form").style.display = "none";
            document.getElementById("squad-form").style.display = "inline-block";
        }
        this.closeAlert()
    }

    changeName(e) {
        var tempFirst =  document.getElementById("firstNameInput").value;
        var tempLast = document.getElementById("lastNameInput").value;

        if (this.sendUpdateRequest({"first_name": this.state.firstName, "last_name": this.state.lastName})) {
            this.setState({firstName: tempFirst, lastName: tempLast});
            this.SuccessMessage();
        } else {
            this.FailureMessage("fail");
        }
    }

    changeEmail(e) {
        var tempEmail =  document.getElementById("emailInput").value;
        if (this.sendUpdateRequest({"email": tempEmail})) {
            this.setState({email: tempEmail});
            this.SuccessMessage();
        } else {
            this.FailureMessage("fail");
        }
    }

    changePhone(e) {
        var tempPhone =  this.state.input;
        console.log(tempPhone);
        if (this.sendUpdateRequest({"phone": tempPhone})) {
            this.setState({phone: tempPhone, input: ""});
            this.SuccessMessage();
        } else {
            this.FailureMessage("fail");
        }
    }

    changeSquad(e) {
        var tempSquad = document.getElementById("squadInput").value;
        if (this.sendUpdateRequest({"squad": tempSquad})) {
            this.setState({curSquad: tempSquad});
            this.SuccessMessage();
        } else {
            this.FailureMessage("fail");
        }
    }

    closeAlert() {
        this.setState ({alert: {
            style: {display: "none"}}
        });
    }

    SuccessMessage() {
        this.setState ({alert: {
            style: {display: "block"},
            severity: "success",
            userMessage: "SUCCESS"}
        });
    }

    FailureMessage(msg) {
        this.setState ({alert: {
            style: {display: "block"},
            severity: "error",
            userMessage: msg}
        });
    }


    render() {
        return(
            <div className="page-display">

                <EndzoneNav links={[{title: "Return to Coaches Corner", link: "/endzone/coaches/hub"},]}></EndzoneNav>
                <div className='alert-div' id="alert" style={this.state.alert.style}>
                    <Alert variant="filled" severity={this.state.alert.severity}
                            action={
                                <IconButton
                                    aria-label="close"
                                    color="inherit"
                                    size="large"
                                    onClick={() => {
                                        this.closeAlert();
                                    }}
                                >
                                    <CloseIcon fontSize="inherit"/>
                                </IconButton>
                            }>
                            {this.state.alert.userMessage}
                    </Alert>
                </div>
                <div className="body-container">
                    <div className="sidebar-nav">
                        <button onClick={() => this.renderForm("profile")}>Profile</button>
                        <button onClick={() => this.renderForm("name")}>Edit Name</button>
                        <button onClick={() => this.renderForm("email")}>Change Email</button>
                        <button onClick={() => this.renderForm("phone")}>Change Phone Number</button>
                        <button onClick={() => this.renderForm("squad")}>Set Default Squad</button>
                    </div>

                    <div className="middle-body-container">

                        <div className="profile-container" id="profile-display">
                            <div className="profile-display-item">
                                <p className="profile-description">First Name:</p>
                                <p className="profile-value">{this.state.firstName}</p>
                            </div>
                            <div className="profile-display-item">
                                <p className="profile-description">Last Name:</p>
                                <p className="profile-value">{this.state.lastName}</p>
                            </div>

                            <div className="profile-display-item">
                                <p className="profile-description">Email:</p>
                                <p className="profile-value">{this.state.email}</p>
                            </div>

                            <div className="profile-display-item">
                                <p className="profile-description">Phone:</p>
                                <p className="profile-value">{this.state.phone}</p>
                            </div>

                            <div className="profile-display-item">
                                <p className="profile-description">Selected Squad:</p>
                                <p className="profile-value">{this.state.curSquad}</p>
                            </div>
                        </div>


                    
                        <form className="register-form" id="name-form">
                            <EndzoneTextInput label="First Name:" id="firstNameInput" textStyle={{"font-size": "5vh"}} labelStyle={{"font-size": "5vh"}}></EndzoneTextInput>
                            <br/>
                            <EndzoneTextInput label="Last Name:" id="lastNameInput" textStyle={{"font-size": "5vh"}} labelStyle={{"font-size": "5vh"}}></EndzoneTextInput>
                            <br/>
                            
                            <button className="endzone-btn" type="button" onClick={(e) => this.changeName(e)}>Submit</button>
                        </form>
            

                        <form className="register-form" id="email-form">
                            <EndzoneTextInput label="Email:" id="emailInput" textStyle={{"font-size": "5vh"}} labelStyle={{"font-size": "5vh"}}></EndzoneTextInput>
                            <br/>

                            <button className="submit-button" type="button" onClick={(e) => this.changeEmail(e)}>Submit</button>
                        </form>

                        <form className="register-form" id="phone-form">
                            <label className="input-title">Phone Number:</label>
                            <div className="phone-container">
                                <div className='phone-input'>
                                    <PhoneInput defaultCountry="US" id="phoneInput" placeholder="Enter phone number" 
                                        numberInputProps={{"style": {"font-size": "5vh", "background": "#353b48", "color": "white", 
                                        "border": "1px solid #0984e3", "border-radius": "2vh", "margin-bottom": "1vh"}}} 
                                        style={{"font-size": "5vh"}} onChange={(e) => this.setState({input: e})}/>
                                </div>
                                
                            </div>
                        
                            <button className="submit-button" type="button" onClick={(e) => this.changePhone(e)}>Submit</button>
                        </form>
                        
                        <form className="register-form" id="squad-form">
                            <EndzoneComboBox id="squadInput" label="Current Squad:" options={this.state.squadsList} labelStyle={{"font-size": "5vh"}} comboStyle={{"font-size": "5vh"}}></EndzoneComboBox>
                            
                            <div className="button-container">
                                <button className="submit-button" type="button" onClick={(e) => this.changeSquad(e)}>Submit</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        );
    }
}



export default DisplayProfile;